.header {
  @media (min-resolution: 2dppx) {
    background-image: url(../img/screen-bg-2x.jpg);
  }

  @media (min-width: 480px) {
    background-position: center top;
  }

  background: url("../img/screen-bg.jpg") 35% top no-repeat $black;
  background-size: cover;
  color: $white;
  position: relative;
  z-index: 1;

  &::before {
    background: linear-gradient(180deg, rgba(0, 0, 0, .5) 0%, rgba(255, 255, 255, 0) 48%, rgba(0, 0, 0, .5) 100%), rgba(24, 24, 24, .8);
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
  }

  .header__wrap {
    @media (min-width: 1024px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

      padding-top: 5px;

    .header__title {
      @media (min-width: 768px) {
        font-size: px-to-rem(44px);
        line-height: px-to-rem(53px);
      }
      @media (min-width: 1024px) {
        font-size: px-to-rem(54px);
        line-height: px-to-rem(64px);
        padding-top: 85px;
        margin-bottom: 12px;

      }

      font-size: px-to-rem(32px);
      line-height: px-to-rem(43px);
      color: $primary;
      text-align: center;
      margin-top: 125px;
      margin-bottom: 24px;
      font-weight: 700;
    }

    .header__subtitle {
      @media (min-width: 768px) {
        font-size: px-to-rem(28px);
        line-height: px-to-rem(32px);
      }
      @media (min-width: 1024px) {
        font-size: px-to-rem(32px);
        line-height: px-to-rem(37px);
        padding-top: 12px;
        margin-bottom: 30px;
      }

      font-size: px-to-rem(24px);
      line-height: px-to-rem(28px);
      text-align: center;
      margin: 24px 0 64px 0;
      font-weight: 400;
    }

    .header__buttons {
      @media (min-width: 580px) {
        display: flex;
        align-items: center;
        justify-content: center;
        margin:30px 0 95px;
      }

      .header__lider {
        @media (min-width: 580px) {
          flex-basis: 288px;
          flex-shrink: 0;
          margin: 0;
        }

          background: url("../img/rectangle.png") center no-repeat;
        background-size: 288px 54px;
        font-size: px-to-rem(14px);
        line-height: px-to-rem(54px);
        display: flex;
        justify-content: space-between;
        max-width: 288px;
        margin: 35px auto 95px;
        position: relative;

        &::after {
          @media (min-resolution: 2dppx) {
            background-image: url(../img/medal-2x.png);
            background-size: cover;
          }

          content: "";
          background: url("../img/medal.png") center no-repeat;
          width: 82px;
          height: 83px;
          position: absolute;
          left: 130px;
          top: 50%;
          transform: translateY(-50%);
        }

        span {
          &:nth-child(1) {
            margin-left: 30px;
          }
          &:nth-child(2) {
            margin-right: 30px;
          }
        }
      }

      .header__work {
        @media (min-width: 580px) {
          flex-basis: 264px;
          max-width: 264px;
          margin-right: 23px;
        }

        @include btn {
          padding-top: 19px;
          padding-bottom: 19px;
        }
      }
    }
  }

  .header__wrap-two {
    padding-bottom: 30px;


    .header__list {
      margin-bottom: 0;
      padding: 0;
    }
  }
}