.calc__radio {

  @media (min-width: 480px) {
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }

  margin-top: 7px;
  margin-bottom: 7px;

  input[type="radio"] {
    display: none;

    &:checked {
      + label {

        &::before {
          content: "";
          width: 8px;
          height: 3px;
          border-left: 2px solid $primary;
          border-bottom: 2px solid $primary;
          position: absolute;
          left: 3px;
          top: 8px;
          transform: rotate(-45deg);
          z-index: 1;
        }

      }
    }

  }

  label {
    font-size: px-to-rem(17px);
    line-height: px-to-rem(24px);
    position: relative;
    padding-left: 25px;
    cursor: pointer;
    display: inline-block;

    &::after {
      width: 16px;
      height: 16px;
      border: 1px solid $primary;
      box-sizing: border-box;
      content: "";
      background-color: transparent;
      border-radius: 5px;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.calc__group {
  padding: 0;
  margin: 20px 0;
  border: none;

  .calc__title {
    @media (min-width: 480px) {
      flex-basis: 100%;
      width: 100%;
    }

    font-size: px-to-rem(14px);
    line-height: px-to-rem(16px);
    font-weight: 700;
    margin-bottom: 10px;
  }

  .calc__range {
    @media (min-width: 1024px) {
      display: flex;
      align-items: center;
      flex-basis: 100%;
      margin-top: 7px;
      margin-bottom: 7px;
    }

    display: none;

    label {
      @include visually-hidden;
    }

    output {
      @media (min-width: 1024px) {
        background-color: transparent;
        display: block;
        width: 67px;
        border-radius: 5px;
        border: 1px solid rgba(255, 255, 255, .2);
        text-align: center;
        font-size: px-to-rem(14px);
        line-height: px-to-rem(16px);
        color: $white;
        padding: 3px 0;
        margin-right: 24px;
      }
    }

    input[type="range"] {
      @media (min-width: 1024px) {
        width: 280px;
        flex-basis: 280px;
        margin: 10px 0;
        appearance: none;

        &::-webkit-slider-runnable-track {
          background-color: $white;
          width: 100%;
          height: 4px;
          cursor: pointer;
        }

        &::-moz-range-track {
          background-color: $white;
          width: 100%;
          height: 4px;
          cursor: pointer;
        }

        &::-ms-track {
          background-color: $white;
          width: 100%;
          height: 4px;
          cursor: pointer;
        }

        &::-webkit-slider-thumb {
          width: 8px;
          height: 8px;
          background-color: $primary;
          border-radius: 8px;
          cursor: pointer;
          appearance: none;
          margin-top: -2px;
        }
        &::-moz-range-thumb {
          width: 8px;
          height: 8px;
          background-color: $primary;
          border-radius: 8px;
          cursor: pointer;
          appearance: none;
          margin-top: -2px;
        }
        &::-ms-thumb {
          width: 8px;
          height: 8px;
          background-color: $primary;
          border-radius: 8px;
          cursor: pointer;
          appearance: none;
          margin-top: -2px;
        }
      }
    }
  }

  .calc__square {
    background-color: transparent;
    display: block;
    width: 100%;
    border-radius: 5px;
    border: 1px solid rgba(255, 255, 255, .2);
    text-align: center;
    font-size: px-to-rem(14px);
    line-height: px-to-rem(16px);
    color: $white;
    padding: 12px 0;
  }
}

.calc__btn {
  @media (min-width: 480px) {
    flex-basis: 164px;
    width: 164px;
    margin-right: 25px;
  }

  @include btn;
}

.calc__result {
  @media (min-width: 480px) {
    text-align: left;
    font-size: px-to-rem(14px);
    line-height: px-to-rem(16px);
  }
  text-align: center;
  font-size: px-to-rem(17px);
  line-height: px-to-rem(24px);
  margin: 20px 0;
}

.calc__wrap {
  @media (min-width: 480px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
}


.calc__input {
  @media (min-width: 480px) {
    flex-basis: 100%;
    width: 100%;
  }
  @media (min-width: 1024px) {
    display: none;
  }
}

.calc {
    @media (min-width: 1024px) {
      background-color: $black;
      border-right: 5px;
    }
}

